* {
  margin: 0;
  padding: 0;
}

:focus {
  outline: none;
}

body {
  scroll-behavior: smooth;
}

img {
  width: 100%;
  height: auto;
  line-height: 0;
  display: block;
}

a {
  text-decoration-line: none;
}

ul {
  list-style-type: none;
}

button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  display: block;
}

* {
  transition: all .2s cubic-bezier(.21, .24, .58, .98);
}

html {
  scroll-behavior: smooth;
}

.main {
  background-color: #282828;
}

.sidebar {
  background-color: #313131;
  width: 400px;
  padding: 40px;
  display: block;
}

.sidebar__list {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.sidebar__item {
  background-color: #282828;
}

.sidebar__img {
  width: 150px;
  height: 150px;
}
